/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.2s ease-out;
}

.modal-content {
  background: white;
  padding: 30px;
  border-radius: 12px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  text-align: center;
  animation: slideUp 0.2s ease-out;
}

.modal-title {
  color: #5e548e;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.modal-text {
  color: #555;
  margin-bottom: 25px;
  line-height: 1.5;
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.modal-btn {
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-size: 0.95rem;
  transition: all 0.2s;
}

.modal-btn.confirm {
  background-color: #5e548e;
  color: white;
}

.modal-btn.confirm:hover {
  background-color: #4a4075;
}

.modal-btn.cancel {
  background-color: #f0f0f0;
  color: #666;
}

.modal-btn.cancel:hover {
  background-color: #e0e0e0;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
