.detail-page {
  max-width: 800px;
  margin: 0 auto;
  animation: fadeIn 0.5s ease-in-out;
}

.detail-title {
  color: #5e548e;
  margin-bottom: 30px;
  font-size: 2rem;
  border-bottom: 2px solid #f0f0f0;
  padding-bottom: 15px;
}

.detail-content section {
  margin-bottom: 40px;
}

.detail-content h3 {
  color: #4a4075;
  font-size: 1.5rem;
  margin-bottom: 20px;
  position: relative;
  padding-left: 15px;
}

.detail-content h3::before {
  content: "";
  position: absolute;
  left: 0;
  top: 5px;
  bottom: 5px;
  width: 4px;
  background-color: #5e548e;
  border-radius: 2px;
}

.detail-content p {
  line-height: 1.8;
  color: #444;
  margin-bottom: 15px;
  font-size: 1.05rem;
}

/* FAQ Section */
.faq-item {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 15px;
  border-left: 3px solid #ddd;
}

.faq-item h4 {
  margin-top: 0;
  color: #333;
  margin-bottom: 10px;
}

/* Conditions List */
.condition-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.condition-item {
  display: flex;
  gap: 20px;
  align-items: flex-start;
}

.condition-num {
  background-color: #5e548e;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  flex-shrink: 0;
  margin-top: 5px;
}

.condition-text h4 {
  margin: 0 0 8px 0;
  color: #333;
}

.condition-text p {
  margin: 0;
  font-size: 0.95rem;
  color: #666;
}

/* Check List */
.check-list {
  list-style: none;
  padding: 0;
}

.check-list li {
  position: relative;
  padding-left: 30px;
  margin-bottom: 12px;
  line-height: 1.6;
}

.check-list li::before {
  content: "✓";
  position: absolute;
  left: 0;
  color: #5e548e;
  font-weight: bold;
}

/* Clues Grid */
.clues-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}

.clue-card {
  background-color: #fff;
  border: 1px solid #eee;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.03);
}

.clue-card h4 {
  color: #5e548e;
  margin-top: 0;
  margin-bottom: 10px;
}

.clue-card p {
  font-size: 0.9rem;
  margin-bottom: 0;
  color: #555;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
